.plus{
    height: 41px;
    border-radius: 4px;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    width: 50px;
    background-color: rgb(236, 112, 11);
    display:flex;
    align-items: center;
    justify-content: center;
}
.searchInputs {
    display: flex;
  }
  
  .search input {
    background-color: rgba(63,59,58,255)
    ;
    border: 0;
    border-radius: 4px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
    font-size: 18px;
    padding: 15px;
    height: 41px;
    width: 300px;
  }
  
  .searchIcon {
    height: 41px;
    border-radius: 4px;
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;
    width: 50px;
    background-color: rgb(236, 112, 11);
    display:flex;
    align-items: center;
    justify-content: center;
  }
  
  input:focus {
    outline: none;
  }
  .searchIcon svg {
    font-size: 30px;
  }
  
  .dataResult {
    margin-top: 5px;
    height: 100px;
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    overflow: hidden;
    overflow-y: auto;
  }
  
  .dataResult::-webkit-scrollbar {
    display: none;
  }
  
  .dataResult .dataItem {
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    color: black;
  }
  
  .dataItem p {
    margin-left: 10px;
  }
  a {
    text-decoration: none;
  }
  
  .dataItem:hover {
    background-color: lightgrey;
  }
  
  #clearBtn {
    cursor: pointer;
  }

