}

.community-form-submit{
    border-radius: 4px;
    background: rgb(236, 112, 11);
    padding: 10px 22px;
    height: 41px;
    width:30%;
    font-size: 19px;
    color: #fff;
    border: none;
    outline: none;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    text-decoration: none;
    margin-top:10px;
}


