/* override css from react-draft-wywsgi
https://github.com/jpuri/react-draft-wysiwyg/blob/master/src/renderer/Image/styles.css
*/

.rdw-image-alignment-options-popup {
  position: absolute;;
  background: white;
  display: flex;
  padding: 5px 2px;
  border-radius: 2px;
  border: 1px solid #F1F1F1;
  width: 90px;
  cursor: pointer;
  z-index: 100;
}
.rdw-image-alignment-option {
  height: 20px;
  width: 20px;
  min-width: 20px;
}

.loader {
  width: 100%;
  height: 49px;
  text-align: center;
}

.loader-spinning-wheel {
  width: 49px;
  height: 49px;
  margin: 0 auto;
  border: 5px solid #cccccc;
  border-radius: 50%;
  border-top: 5px solid #1d4d8b;
  -webkit-animation: loader-spin infinite 1s linear;
          animation: loader-spin infinite 1s linear;
}

@-webkit-keyframes loader-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes loader-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.statusMessage-default {
  text-align: center;
  width: 33%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 40px;
}

.statusMessage-modal {
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

